module.exports = ".switch {\n  position: relative;\n  display: inline-block;\n  width: 35px;\n  height: 15px;\n  margin: 0px 5px 0 5px;\n}\n\n.switch input {\n  display: none;\n  background-color: #ccc;\n}\n\n.slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0px;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  transition: 0.4s;\n}\n\n.slider:before {\n  position: absolute;\n  content: \"\";\n  height: 20px;\n  width: 20px;\n  left: 0px;\n  right: 0px;\n  bottom: -3px;\n  background-color: #151d24;\n  transition: 0.4s;\n}\n\ninput:checked + .slider:before {\n  transform: translateX(20px);\n}\n\n/* Rounded sliders */\n\n.slider.round {\n  border-radius: 20px;\n}\n\n.slider.round:before {\n  border-radius: 50%;\n}\n\n.label-text {\n  margin: 4px 5px 5px 5px;\n  font-size: 13px;\n}\n/*# sourceMappingURL=data:application/json;base64, */"